import React from 'react'
import AddNewLink from '../components/AddNewLink'
import LinkList from '../components/LinkList'
import Layout from '../components/layout'
import uuid from 'uuid'

class IndexPage extends React.Component {
  state = {
    links: [],
    filtered: [],
  }

  constructor(props) {
    super(props)
    this.input = React.createRef()
  }

  filter = value => {
    this.setState(
      {
        filtered: this.state.links.filter(
          link => link.link.includes(value) || link.description.includes(value)
        ),
      },
      () => this.setState({ add: this.state.filtered.length < 1 })
    )
  }

  componentDidMount() {
    let links = localStorage.getItem('links')
    links = JSON.parse(links)
    if (links) {
      this.setState({ links, filtered: links })
    }
    this.input.current.focus()
  }

  addLink = ({ link, description }) => {
    debugger
    this.setState(
      { links: [...this.state.links, { link, description, id: uuid() }] },
      () => {
        localStorage.setItem('links', JSON.stringify(this.state.links))
        this.setState({ filtered: this.state.links, add: false })
        this.input.current.focus()
      }
    )
  }

  render() {
    return (
      <Layout>
        <AddNewLink
          add={this.state.add}
          filter={this.filter}
          addLink={this.addLink}
          reference={this.input}
        />
        <LinkList links={this.state.filtered} />
      </Layout>
    )
  }
}

export default IndexPage
