import React, { Component } from 'react'

const Link = ({ link, description }) => (
  <div className="rounded bg-blue p-4 h-16 rounded-sm border border-black-100 border-solid">
    <a rel="noopener noreferrer" className="font-sm" target="_blank" href={link}>
      <span>{link}</span>
    </a>
      <p>{description}</p>
  </div>
)

class LinkList extends Component {
  render() {
    return (
      <div className="md:flex-wrap md:w-full md:flex">
         {this.props.links.length < 1 && <span>No links... Adding new one.</span>}
        {this.props.links.map(({ link, description, id }) => (
          <Link key={`${id}`} {...{ link, description }} />
        ))}
      </div>
    )
  }
}
export default LinkList
