import React, { Component } from 'react'

class AddNewLink extends Component {
  state = { link: '', description: '' }
  handleSubmit = e => {
    e.preventDefault()
    debugger
    this.props.addLink({
      description: this.state.description,
      link: this.state.link,
    })
    this.setState({ link: '', description: '' })
  }

  handleChange = e => {
    const inputName = e.target.name
    const currentLink = this.state.link

    this.setState(
      { [e.target.name]: e.target.value },
      (prevState) => {
        const isDeleting = ( () =>{ return this.state.link.length < currentLink.length} )();
        const isDescriptionEmpty = ( () => { return this.state.description.length < 1} )();
       (!this.props.add || (isDeleting && isDescriptionEmpty)) && this.props.filter(this.state[inputName])
      }
    )
  }

  render() {
    const addnew = this.props.add
    return (
      <div className="py-2 w-full md:w-1/2 md:mx-auto">
        <form className="flex" onSubmit={this.handleSubmit}>
          <input
            ref={this.props.reference}
            name="link"
            value={this.state.link}
            className={ `${this.props.add ? 'w-2/5' : 'w-full'} md:w-full p-2 w-12 border-2 border-solid` }
            onChange={this.handleChange}
            placeholder="Type to search or paste new link"
          />
          {addnew && (
            <input
              name="description"
              value={this.state.description}
              placeholder="Description"
              className="w-2/5 p-2 max-w-card border-2 border-solid"
              onChange={this.handleChange}
            />
          )}
          {addnew && (
            <input
              className="w-1/5 p-2 md:w-1/4"
              type="submit"
              value="Dodaj"
            />
          )}
        </form>
      </div>
    )
  }
}
export default AddNewLink
